const ButtonTheme = createMuiTheme({
  palette: {
    primary: green,
  },
});

const headCells: HeadCell[] = [
  {
    id: "id",
    numeric: true,
    label: "ID",
  },
  { id: "name", numeric: false, label: "Name" },
];

const createData = ({ id, name }: Data) => {
  return { id, name };
};

function EnhancedTableHead(props: EnhancedTableProps) {
  const { order, orderBy, onRequestSort } = props;

  const classes = useStyles();

  const createSortHandler = (property: keyof Data) => (
    event: React.MouseEvent<unknown>
  ) => {
    onRequestSort(event, property);
  };

  return (
    <TableHead className={classes.tableHead}>
      <TableRow>
        {headCells.map((headCell, index) => {
          return (
            <StyledTableCell
              key={headCell.id}
              align={headCell.numeric ? "right" : "left"}
              sortDirection={orderBy === headCell.id ? order : false}
            >
              <TableSortLabel
                active={orderBy === headCell.id}
                direction={orderBy === headCell.id ? order : "asc"}
                onClick={createSortHandler(headCell.id)}
                className={
                  (orderBy === headCell.id ? classes.sortLabelActive : "",
                  classes.sortLabelRoot)
                }
                // style={{ color: "#ffffff" }}
              >
                {headCell.label}
              </TableSortLabel>
            </StyledTableCell>
          );
        })}
        <StyledTableCell key={"profiles"} align="center">
          <StyledColumnLabel>Actions</StyledColumnLabel>
        </StyledTableCell>
      </TableRow>
    </TableHead>
  );
}

const ProfileTable: React.FC = () => {
  const [data, setData] = useState<Data[]>([]);
  const { profilesList, rerender } = useSelector(selector);
  const dispatch = useDispatch();
  const history = useHistory();

  const [order, setOrder] = useState<Order>("desc");
  const [orderBy, setOrderBy] = useState<keyof Data>("id");
  const [page, setPage] = useState<number>(0);
  const [rowsPerPage, setRowsPerPage] = useState<number>(25);

  const [searchText, setSearchText] = useState("");

  const classes = useStyles();
  useEffect(() => {
    const api = GET_PROFILES;
    fetch(api, {
      method: "POST",
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        const result: Data[] = data.map((el: any) => {
          el.id = parseInt(el.id);
          return createData(el);
        });
        dispatch(updateList(result));
        setData(result);
      });
  }, [dispatch, rerender]);

  useEffect(() => {
    setData(profilesList);
  }, [profilesList]);

  const handleRequestSort = (event: any, property: keyof Data) => {
    const isAsc = orderBy === property && order === "asc";
    setOrder(isAsc ? "desc" : "asc");
    setOrderBy(property);
  };

  const handleChangePage = (event: any, newPage: any) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event: any) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleSearch = (event: any) => {
    const value = event.target.value.toLowerCase();
    setSearchText(value);
    if (!value || value.length === 0) {
      setData(profilesList);
      return;
    }
    let result = profilesList.filter(
      (elm: any) =>
        elm.id.toString().includes(value) ||
        elm.name.toLowerCase().includes(value)
    );
    setPage(0);
    setData(result);
  };

  const handleAction = (id: number, action: string) => () => {
    let api = "";
    if (action === "clone") {
      api = CLONE_PROFILE;
    } else {
      // action === "delete"
      api = DELETE_PROFILE;
    }
    const body = {
      id: id.toString(),
    };
    fetch(api, {
      method: "POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
      },
      body: qs.stringify(body),
    });
    if (action === "clone") {
      dispatch(requestRerender());
    } else {
      dispatch(deleteProfile(id));
    }
  };

  return (
    <div>
      <div className={classes.flexDiv}>
        <div className={classes.flexDiv2}>
          {/* <Typography style={{ margin: "auto", paddingRight: "5px" }}>
            Filter:{" "}
          </Typography> */}
          <TextField
            id="filter"
            autoComplete="off"
            autoCorrect="off"
            spellCheck="false"
            variant="outlined"
            value={searchText}
            onChange={handleSearch}
            inputProps={{
              style: {
                margin: "auto",
                width: "35ch",
                padding: "6px 6px 7px ",
              },
            }}
            InputProps={{
              startAdornment: (
                <InputAdornment position="start">
                  <Typography>Filter: </Typography>
                </InputAdornment>
              ),
            }}
          />
        </div>
        <aside>
          <Button
            variant="outlined"
            color="primary"
            // onClick={handleOpenModal}
            startIcon={<AddIcon />}
          >
            Add Profile
          </Button>
        </aside>
      </div>
      <TableContainer style={{ width: "100%", overflow: "hidden" }}>
        <Table
          aria-labelledby="tableTitle"
          size="small"
          // size={dense ? "small" : "medium"}
          aria-label="enhanced table"
        >
          <colgroup>
            <col style={{ width: "7%" }} />
            <col style={{ width: "78%" }} />
            <col style={{ width: "15%" }} />
          </colgroup>
          <EnhancedTableHead
            order={order}
            orderBy={orderBy}
            onRequestSort={handleRequestSort}
          />
          <ThemeProvider theme={ButtonTheme}>
            <TableBody>
              {data.length === 0 ? (
                <TableRow hover>
                  <TableCell colSpan={6}>No matching records found</TableCell>
                </TableRow>
              ) : (
                stableSort(data, getComparator(order, orderBy))
                  .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  .map((row: any, index: number) => {
                    return (
                      <StyledTableRow
                        tabIndex={-1}
                        key={row.id}
                        className={classes.tableRow}
                        hover
                      >
                        <StyledTableCell
                          component="th"
                          align="right"
                          scope="row"
                        >
                          {row.id}
                        </StyledTableCell>
                        <StyledTableCell align="left">
                          <Link
                            to={`/admin/profiles/detail/${row.id}`}
                            className={classes.profileLinks}
                          >
                            {row.name}
                          </Link>
                        </StyledTableCell>
                        <StyledTableCell align="center">
                          {" "}
                          <div className={classes.flexDiv2}>
                            {/* <Tooltip title="Edit">
                              <IconButton
                                aria-label="edit"
                                color="primary"
                                size="small"
                                onClick={() =>
                                  history.push(
                                    "/admin/profiles/detail/" + row.id
                                  )
                                }
                              >
                                <EditIcon />
                              </IconButton>
                            </Tooltip> */}
                            <Tooltip title="Clone">
                              <IconButton
                                aria-label="clone"
                                color="inherit"
                                size="small"
                                onClick={handleAction(row.id, "clone")}
                              >
                                <Copy />
                              </IconButton>
                            </Tooltip>
                            <Tooltip title="Delete">
                              <IconButton
                                component="div"
                                aria-label="delete"
                                color="secondary"
                                size="small"
                                onClick={handleAction(row.id, "delete")}
                              >
                                <DeleteIcon />
                              </IconButton>
                            </Tooltip>
                          </div>
                        </StyledTableCell>
                      </StyledTableRow>
                    );
                  })
              )}
            </TableBody>
          </ThemeProvider>
        </Table>
      </TableContainer>
      <div style={{ display: "flex", justifyContent: "center" }}>
        <TablePagination
          align="center"
          rowsPerPageOptions={[10, 25, 50]}
          component="div"
          count={data.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
          //   labelDisplayedRows={({ from, to, count }) => {
          //     return {""};
          //   }}
          //   backIconButtonProps={{
          //     "aria-label": "Previous Page",
          //   }}
          //   nextIconButtonProps={{
          //     "aria-label": "Next Page",
          //   }}
        />
        {/* <Pagination
          count={(data.length / 25) >> 0}
          color="primary"
          page={page}
          onChange={handleChangePage}
          //   defaultPage={1}
          defaultChecked
        /> */}
      </div>
    </div>
  );
};

export default ProfileTable;
